"use strict";

import preload from "./modules/preload.js";
import hamburger from "./modules/hamburger.js";
import showStyles from "./modules/styles.js";
import showProducts from "./modules/products.js";
import showSmallMenu from "./modules/smallMenu.js";
import showTopMenuAndArrows from "./modules/showTopMenu.js";
import { stylesImg, stylesLink } from "./services/variables.js";
import "./services/itc-slider.min.js";

window.addEventListener("DOMContentLoaded", () => {
	preload(".overlay", "body", "style");
	window.addEventListener("scroll", showTopMenuAndArrows);

	if (window.innerWidth >= 992) {
		listener("click");
	} else {
		listener("touchend");
	}

	function listener(action) {
		window.addEventListener(action, (e) => {
			if (
				e.target.classList.contains("style__show") ||
				e.target.closest(".btn__styles")
			) {
				showStyles(
					e.target,
					".btn__styles",
					"styles__link",
					"styles__title",
					"style__show"
				);
			} else if (e.target.closest(".products")) {
				showProducts(
					"menu__full_white",
					"menu__forbigproduct",
					"show",
					"overflow_hidden"
				);
			} else if (e.target.closest(".menu__subtitle")) {
				showSmallMenu(e.target, "show", "height", "overflow_scroll");
			} else if (
				e.target.closest(".hamburger") ||
				e.target.classList.contains(".hamburger")
			) {
				hamburger(
					"menu__full_white",
					"menu__forbigproduct",
					"menu__big_show",
					"menu__list_hide",
					"menu__display_none",
					"overflow_scroll",
					"overflow_hidden",
					"show",
					"height"
				);
			}
		});
	}

	stylesLink.forEach((item, i) => {
		item.addEventListener("mouseover", () => {
			stylesImg[i].classList.add("show");
		});
		item.addEventListener("mouseout", () => {
			stylesImg[i].classList.remove("show");
		});
	});
	const featuresTitle = document.querySelectorAll(".features__item-titles");

	featuresTitle.forEach((title) => {
		title.addEventListener("click", () => {
			const content = title.nextElementSibling;
			const featuresSpans = title.querySelector(".spans");

			if (content && content.classList.contains("features__item-content")) {
				content.classList.toggle("show-content");
				featuresSpans.classList.toggle("create-cross");
			} else if (
				content &&
				content.classList.contains("features__item-wrapper")
			) {
				// Если content имеет класс features__item-wrapper
				const itemContents = content.querySelectorAll(
					".features__item-content"
				);
				featuresSpans.classList.toggle("create-cross");
				itemContents.forEach((item) => {
					item.classList.toggle("show-content");
				});
			}
		});
	});

	const coockersList = document.getElementById("cookers-top__list");
	if (coockersList) {
		coockersList.addEventListener("change", (e) => {
			let selectedValue = e.target.value;

			let sections = document.querySelectorAll(".cookers-bottom__type");

			sections.forEach((section) => {
				if (selectedValue === "All") {
					section.style.display = "block";
				} else {
					if (section.id === selectedValue) {
						section.style.display = "block";
					} else {
						section.style.display = "none";
					}
				}
			});
		});
	}

	const galleryPhotosWrapper = document.querySelector(".gallery__photos");

	let usedNumbers = [];

	function getRandomInt(min, max) {
		return Math.floor(Math.random() * (max - min + 1)) + min;
	}

	function createImageElement(imageNumber) {
		const photoDiv = document.createElement("div");
		photoDiv.classList.add("gallery__photo");

		const img = document.createElement("img");
		img.src = `img/gallery/${imageNumber}.webp`;
		img.alt = "photo";
		img.classList.add("gallery__photo-img");

		photoDiv.appendChild(img);
		galleryPhotosWrapper.appendChild(photoDiv);
	}

	function generateGalleryImages() {
		while (usedNumbers.length < 56) {
			const randomImageNumber = getRandomInt(1, 56);

			if (!usedNumbers.includes(randomImageNumber)) {
				usedNumbers.push(randomImageNumber);
				createImageElement(randomImageNumber);
			}
		}
		initializeGallery();
	}

	function initializeGallery() {
		const galleryPhotos = document.querySelectorAll(".gallery__photo-img");

		const bigPhoto = document.querySelector(".gallery__big-photo");
		const bigPhotoImg = document.querySelector(".gallery__big-photo-img");
		const bigPhotoOverlay = document.querySelector(".gallery__overlay");
		const bigPhotoCross = document.querySelector(".gallery__cross");

		galleryPhotos.forEach((photo) => {
			photo.addEventListener("click", () => {
				bigPhotoImg.src = photo.src;

				bigPhoto.classList.add("show");
			});
		});

		bigPhoto.addEventListener("click", (e) => {
			if (
				e.target.classList.contains("gallery__cross") ||
				e.target.classList.contains("gallery__overlay")
			) {
				bigPhoto.classList.remove("show");
			}
		});

		let currentImageIndex = parseInt(bigPhotoImg.src.match(/(\d+)\.webp/)[1]);

		const leftArrow = document.querySelector(".gallery__big-photo-left");
		leftArrow.addEventListener("click", () => {
			currentImageIndex = currentImageIndex > 1 ? currentImageIndex - 1 : 56;
			bigPhotoImg.src = `img/gallery/${currentImageIndex}.webp`;
		});

		const rightArrow = document.querySelector(".gallery__big-photo-right");
		rightArrow.addEventListener("click", () => {
			currentImageIndex = currentImageIndex < 56 ? currentImageIndex + 1 : 1;
			bigPhotoImg.src = `img/gallery/${currentImageIndex}.webp`;
		});
	}

	if (window.location.pathname.includes("gallery.html")) {
		generateGalleryImages();
	}

	const form = document.querySelector(".footer__form");

	form.addEventListener("submit", function (e) {
		e.preventDefault();

		fetch("mailer/smart.php", {
			method: "POST",
			body: new FormData(this),
		})
			.then(function (response) {
				if (response.ok) {
					form.querySelectorAll("input").forEach((input) => {
						console.log(1);
						input.value = "";
					});
					form.reset();
				}
			})
			.catch((error) => {
				console.log("Error:", error);
			});

		return false;
	});
});
